.mainBody {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 10vh;
	height: 90vh;
	padding: 0 8rem;
	background-color: #002054;
}

.logoContainer {
	margin-right: 8rem;
	width: 22.5rem;
	height: 22.5rem;
	overflow: hidden;
	position: relative;
}

.logo {
	position: absolute;
	width: 25rem;
	height: 25rem;
	left: 50%;
	top: 50%;
	-webkit-transform: translate(-50%,-50%);
	-ms-transform: translate(-50%,-50%);
	transform: translate(-50%,-50%);
}

.description {
	flex-direction: column;
	text-align: left;
}

.description2 {
	display: none;
}

.title {
	font-size: 3.3rem;
	font-weight: 500;
	color: #fff;
	margin: 0;
	margin-bottom: .5rem;
	padding: 0;
}

.date {
	font-size: 1.75rem;
	font-weight: 300;
	color: #fff;
	margin: 0;
	margin-bottom: .5rem;
	padding: 0;
}

.subtitle {
	font-size: 1.75rem;
	font-weight: 200;
	color: #fff;
	margin: 0;
	margin-bottom: 2rem;
	padding: 0;
}

.registerButton {
	background-color: #f7c945;
	font-size: 1.5rem;
	font-weight: bold;
	border-radius: 20px;
	padding: 0.7rem 1.2rem;
	color: #002054;
	cursor: pointer;
	text-decoration: none;
}

@media screen and (max-width: 1250px) {
	.mainBody {
		padding: 0 5rem;
	}

	.logoContainer {
		margin-right: 5rem;
	}
}

@media screen and (max-width: 1110px) {
	.mainBody {
		padding: 0 3rem;
	}

	.logoContainer {
		margin-right: 3rem;
	}
}


@media screen and (max-width: 1015px) {
	.logoContainer {
		width: 20rem;
		height: 20rem;
	}
	
	.logo {
		width: 22.5rem;
		height: 22.5rem;
	}

	.title {
		font-size: 3rem;
	}

	.date {
		font-size: 1.5rem;
	}

	.subtitle {
		font-size: 1.5rem;
	}

	.registerButton {
		font-size: 1.25rem;
	}
}

@media screen and (max-width: 900px) {
	.mainBody {
		padding: 0 1rem;
	}

	.logoContainer {
		width: 17.5rem;
		height: 17.5rem;
	}
	
	.logo {
		width: 20rem;
		height: 20rem;
	}

	.title {
		font-size: 2.75rem;
	}

	.date {
		font-size: 1.4rem;
	}

	.subtitle {
		font-size: 1.4rem;
	}

	.registerButton {
		font-size: 1.25rem;
	}
}

@media screen and (max-width: 765px) {
	.mainBody {
		flex-direction: column;
	}

	.logoContainer {
		margin: 0;
		margin-top: -8rem;
		margin-bottom: -3rem;
		width: 20rem;
		height: 20rem;
	}

	.logo {
		width: 20rem;
		height: 20rem;
	}

	.description {
		display: none;
	}

	.description2 {
		display: block;
		text-align: center;
	}

	.date {
		font-size: 1.5rem;
		font-weight: 300;
		font-style: italic;
	}

	.subtitle {
		font-size: 1.75rem;
		font-weight: 300;
	}

	.registerButton {
		font-size: 1.4rem;
	}
}
