.register {
	padding-top: 4rem;
	padding-bottom: 4rem;
	text-align: center;
	background-color: #002054;
	/* background-image: linear-gradient(#002054 90%, #001333 100%); */
	flex-direction: column;
}

.text {
	font-size: 1.5rem;
	padding-bottom: 1rem;
	color: #fff;
}
.registerButton {
	background-color: #f7c945;
	font-size: 1.5rem;
	font-weight: bold;
	border-radius: 20px;
	padding: 0.7rem 1.2rem;
	color: #002054;
	cursor: pointer;
	text-decoration: none;
}
